/**
 * ============================================================================
 * DATA ITEM
 * ============================================================================
 * @hidden
 */

//@todo: sequenced?

/**
 * Defines a [[DataItem]] for [[PieSeries]].
 *
 * @see {@link DataItem}
 */
export class PieSeriesDataItem extends SeriesDataItem implements ILegendItem<DataItem, IDataItemEvents> {

	/**
	 * A type of slice used for this series.
	 *
	 * @ignore Exclude from docs
	 * @type {Slice}
	 */
	public _slice: Slice;

	/**
	 * A reference to a slice label element.
	 * @ignore Exclude from docs
	 * @type {AxisLabelCircular}
	 */
	protected _label: AxisLabelCircular;

	/**
	 * A reference to a slice tick element.
	 * @ignore Exclude from docs
	 * @type {PieTick}
	 */
	protected _tick: PieTick;

	/**
	 * A reference to a corresponding legend data item.
	 * 
	 * @type {LegendDataItem<DataItem, IDataItemEvents>}
	 */
	protected _legendDataItem: LegendDataItem<DataItem, IDataItemEvents>;

	/**
	 * Custom settings for the legend item.
	 * 
	 * @type {LegendSettings}
	 */
	public legendSettings: LegendSettings;

	/**
	 * Defines a type of [[Component]] this data item is used for.
	 *
	 * @ignore Exclude from docs
	 * @type {PieSeries}
	 */
	public _component: PieSeries;

	/**
	 * Constructor
	 */
	constructor() {
		super();
		this.className = "PieSeriesDataItem";

		this.values.radiusValue = {};

		this.applyTheme();
	}

	/**
	 * Adds an `id` attribute the the slice element and returns its id.
	 *
	 * @ignore Exclude from docs
	 */
	public uidAttr(): string {
		return this.slice.uidAttr();
	}

	/**
	 * Hide the data item (and corresponding visual elements).
	 * 
	 * @param {number}    duration  Duration (ms)
	 * @param {number}    delay     Delay hiding (ms)
	 * @param {number}    toValue   Target value for animation
	 * @param {string[]}  fields    Fields to animate while hiding
	 */
	public hide(duration?: number, delay?: number, toValue?: number, fields?: string[]): Animation {
		return super.hide(duration, delay, 0, ["value", "radiusValue"]);
	}

	/**
	 * Show hidden data item (and corresponding cisual elements).
	 * 
	 * @param {number}    duration  Duration (ms)
	 * @param {number}    delay     Delay hiding (ms)
	 * @param {string[]}  fields    Fields to animate while hiding
	 */
	public show(duration?: number, delay?: number, fields?: string[]): Animation {
		return super.show(duration, delay, ["value", "radiusValue"]);
	}

	/**
	 * Color of the slice.
	 *
	 * @todo why not Color?
	 * @param {string}  value  Color
	 */
	public set color(value: string) {
		this.setProperty("color", value);
	}

	/**
	 * @return {string} Color
	 */
	public get color(): string {
		return this.properties.color;
	}

	/**
	 * Category.
	 * 
	 * @param {string}  value  Category
	 */
	public set category(value: string) {
		this.setProperty("category", value);
	}

	/**
	 * @return {string} Category
	 */
	public get category(): string {
		return this.properties.category;
	}


	/**
	 * Slice's radius, if other than default.
	 * 
	 * @param {number}  value  Radius
	 */
	public set radiusValue(value: number) {
		this.setValue("radiusValue", value);
	}

	/**
	 * @return {number} Radius
	 */
	public get radiusValue(): number {
		return this.values.radiusValue.value;
	}

	/**
	 * Creates a marker used in the legend for this slice.
	 *
	 * @ignore Exclude from docs
	 * @param {Container}  marker  Marker container
	 */
	public createLegendMarker(marker: Container) {
		this.component.createLegendMarker(marker, this);
	}

	/**
	 * A legend's data item, that corresponds to this data item.
	 *
	 * @param {LegendDataItem<DataItem, IDataItemEvents>}  value  Legend data item
	 */
	public set legendDataItem(value: LegendDataItem<DataItem, IDataItemEvents>) {
		this._legendDataItem = value;
		value.label.dataItem = this;
		value.valueLabel.dataItem = this;
	}

	/**
	 * @return {LegendDataItem<DataItem, IDataItemEvents>} Legend data item
	 */
	public get legendDataItem(): LegendDataItem<DataItem, IDataItemEvents> {
		return this._legendDataItem;
	}

	/**
	 * A Pie Tick element, related to this data item. (slice)
	 *
	 * @readonly
	 * @return {PieTick} Tick element
	 */
	public get tick(): PieTick {
		if (!this._tick) {
			this._tick = this.component.ticks.create();
			this.addSprite(this._tick);
			this._tick.slice = this.slice;
			this._tick.label = this.label;
		}
		return this._tick;
	}

	/**
	 * A Label element, related to this data item. (slice)
	 *
	 * @readonly
	 * @return {AxisLabelCircular} Label element
	 */
	public get label(): AxisLabelCircular {
		if (!this._label) {
			this._label = this.component.labels.create();
			this.addSprite(this._label);
		}
		return this._label;
	}

	/**
	 * A Slice element, related to this data item. (slice)
	 *
	 * @readonly
	 * @return {Slice} Slice element
	 */
	public get slice(): this["_slice"] {
		if (!this._slice) {
			this._slice = this.component.slices.create();
			this.addSprite(this._slice);
		}
		return this._slice;
	}

	/**
	 * Should dataItem (slice) be hidden in legend?
	 * 
	 * @param {boolean} value Visible in legend?
	 */
	public set hiddenInLegend(value: boolean) {
		this.setProperty("hiddenInLegend", value);
	}

	/**
	 * @return {boolean} Disabled in legend?
	 */
	public get hiddenInLegend(): boolean {
		return this.properties.hiddenInLegend;
	}
}


/**
 * ============================================================================
 * REQUISITES
 * ============================================================================
 * @hidden
 */

/**
 * Defines data fields for [[PieSeries]].
 */
export interface IPieSeriesDataFields extends ISeriesDataFields {

	/**
	 * Name of the field in data that holds category.
	 * 
	 * @type {string}
	 */
	category?: string;

	/**
	 * Name of the field in data that holds boolean flag if item should be
	 * hidden in legend.
	 * 
	 * @type {string}
	 */
	hiddenInLegend?: string;

	/**
	 * Name of the field in data that holds item's radius value.
	 * 
	 * @type {string}
	 */
	radiusValue?: string;

}

/**
 * Defines properties for [[PieSeries]].
 */
export interface IPieSeriesProperties extends ISeriesProperties {

	/**
	 * Outer radius for the series' slices in pixels.
	 *
	 * @ignore Exclude from docs
	 * @todo Redo so that users can set it
	 * @type {number}
	 */
	radius?: number;

	/**
	 * Inner radius for the series' slices in pixels.
	 *
	 * @ignore Exclude from docs
	 * @todo Redo so that users can set it
	 * @type {number}
	 */
	innerRadius?: number;

	/**
	 * Start angle for the series' slices in degrees. (0-360)
	 *
	 * @ignore Exclude from docs
	 * @todo Redo so that users can set it
	 * @type {number}
	 */
	startAngle?: number;

	/**
	 * End angle for the series' slices in degrees. (0-360)
	 *
	 * @ignore Exclude from docs
	 * @todo Redo so that users can set it
	 * @type {number}
	 */
	endAngle?: number;

	/**
	 * Align labels into nice vertical columns?
	 *
	 * @default true
	 * @type {number}
	 */
	alignLabels?: number;

	/**
	 * A color set to be used for slices.
	 * 
	 * For each new subsequent slice, the chart will assign the next color in
	 * this set.
	 * 
	 * @type {ColorSet}
	 */
	colors?: ColorSet;

}

/**
 * Defines events for [[PieSeries]].
 */
export interface IPieSeriesEvents extends ISeriesEvents { }

/**
 * Defines adapters for [[PieSeries]].
 * 
 * @see {@link Adapter}
 */
export interface IPieSeriesAdapters extends ISeriesAdapters, IPieSeriesProperties { }


/**
 * ============================================================================
 * MAIN CLASS
 * ============================================================================
 * @hidden
 */

/**
 * Defines [[Series]] for a slice series on a Pie chart.
 * 
 * @see {@link IPieSeriesEvents} for a list of available Events
 * @see {@link IPieSeriesAdapters} for a list of available Adapters
 * @todo Example
 * @important
 */
export class PieSeries extends Series {

	/**
	 * Defines the type of data fields used for the series.
	 *
	 * @ignore Exclude from docs
	 * @type {IPieSeriesDataFields}
	 */
	public _dataFields: IPieSeriesDataFields;

	/**
	 * Defines available properties.
	 *
	 * @ignore Exclude from docs
	 * @type {IPieSeriesProperties}
	 */
	public _properties: IPieSeriesProperties;

	/**
	 * Defines available adapters.
	 *
	 * @ignore Exclude from docs
	 * @type {IPieSeriesAdapters}
	 */
	public _adapter: IPieSeriesAdapters;

	/**
	 * Event dispacther.
	 * 
	 * @type {SpriteEventDispatcher<AMEvent<PieSeries, IPieSeriesEvents>>} Event dispatcher instance
	 */
	public events: SpriteEventDispatcher<AMEvent<PieSeries, IPieSeriesEvents>>;

	/**
	 * Defines the type of data item.
	 *
	 * @ignore Exclude from docs
	 * @type {PieSeriesDataItem}
	 */
	public _dataItem: PieSeriesDataItem;

	/**
	 * Holds current angle for the next slice to start on.
	 * 
	 * @type {number}
	 */
	protected _currentStartAngle: number;

	/**
	 * Data items that fall to the left side of the pie.
	 */
	protected _leftItems: this["_dataItem"][];

	/**
	 * Data items that fall to the right side of the pie.
	 */
	protected _rightItems: this["_dataItem"][];

	/**
	 * Container slice elements are put in.
	 *
	 * @ignore Exclude from docs
	 * @type {Container}
	 */
	public slicesContainer: Container;

	/**
	 * Container tick elements are put in.
	 *
	 * @ignore Exclude from docs
	 * @type {Container}
	 */
	public ticksContainer: Container;

	/**
	 * Container label elements are put in.
	 *
	 * @ignore Exclude from docs
	 * @type {Container}
	 */
	public labelsContainer: Container;

	/**
	 * List of slice elements.
	 * 
	 * @type {ListTemplate<Slice>}
	 */
	public slices: ListTemplate<Slice>;

	/**
	 * List of tick elements.
	 * 
	 * @type {ListTemplate<PieTick>}
	 */
	public ticks: ListTemplate<PieTick>;

	/**
	 * List of label elements.
	 * 
	 * @type {ListTemplate<AxisLabelCircular>}
	 */
	public labels: ListTemplate<AxisLabelCircular>;

	/**
	 * [_arcRect description]
	 *
	 * @todo Description
	 * @type {IRectangle}
	 */
	protected _arcRect: IRectangle;

	/**
	 * [_maxRadiusPercent description]
	 *
	 * @todo Description
	 * @type {number}
	 */
	protected _maxRadiusPercent: number;

	/**
	 * Constructor
	 */
	constructor() {
		super();

		this.className = "PieSeries";

		this.alignLabels = true;
		this.startAngle = -90;
		this.endAngle = 270;

		this.colors = new ColorSet();
		this.colors.step = 1;

		let slicesContainer = this.createChild(Container);
		slicesContainer.shouldClone = false;
		slicesContainer.isMeasured = false;
		slicesContainer.layout = "none";
		this.slicesContainer = slicesContainer;

		let ticksContainer = this.createChild(Container);
		ticksContainer.shouldClone = false;
		ticksContainer.isMeasured = false;
		ticksContainer.layout = "none";
		this.ticksContainer = ticksContainer;

		let labelsContainer = this.createChild(Container);
		labelsContainer.shouldClone = false;
		labelsContainer.isMeasured = false;
		labelsContainer.layout = "none";
		this.labelsContainer = labelsContainer;

		this.bulletsContainer.toFront();

		this.initSlice(Slice);

		// Create tick list
		let tick = new PieTick();
		tick.isMeasured = false;
		this.ticks = new ListTemplate<PieTick>(tick);

		// Create labels list
		// @todo create a labelText/labelHTML properties just like
		// tooltipText/tooltipHTML
		let label = new AxisLabelCircular();
		label.text = "{category}: {value.percent.formatNumber('#.0')}%";
		label.isMeasured = false;
		label.radius = 25;
		label.padding(5, 5, 5, 5);
		label.renderingFrequency = 2;
		this.labels = new ListTemplate<AxisLabelCircular>(label);

		// Make all slices focusable
		this.skipFocusThreshold = 50;

		//let hiddenState = this.hiddenState;
		//hiddenState.properties.opacity = 1;
		//hiddenState.properties.endAngle = -90;
		//hiddenState.properties.startAngle = -90;

		let defaultState = this.defaultState;
		defaultState.easing = $ease.sinOut;

		let hoverState = this.slices.template.states.create("hover");
		hoverState.properties.scale = 1.05;

		// Accessibility
		this.itemReaderText = "{category}: {value.percent.formatNumber('#.#')}%";

		this.applyTheme();
	}

	/**
	 * Sets defaults that instantiate some objects that rely on parent, so they
	 * cannot be set in constructor.
	 */
	protected applyInternalDefaults(): void {
		super.applyInternalDefaults();
		if (!$type.hasValue(this.readerTitle)) {
			this.readerTitle = this.language.translate("Pie Slice Series");
		}
	}

	/**
	 * Returns a new/empty DataItem of the type appropriate for this object.
	 * 
	 * @see {@link DataItem}
	 * @return {PieSeriesDataItem} Data Item
	 */
	protected createDataItem(): this["_dataItem"] {
		return new PieSeriesDataItem();
	}

	/**
	 * Creates and returns a new slice element.
	 *
	 * @param  {typeof Slice}  sliceType  Type of the slice element
	 * @return {Slice}                    Slice
	 */
	protected initSlice(sliceType: typeof Slice): Slice {
		// Create a slice template
		let slice = new sliceType();

		slice.isMeasured = false;
		slice.defaultState.properties.scale = 1;
		slice.observe("scale", this.handleSliceScale, this);
		slice.observe(["dx", "dy", "x", "y", "shiftRadius"], this.handleSliceMove, this);
		slice.tooltipText = "{category}: {value.percent.formatNumber('#.#')}% ({value.value})";

		// Create slice hover state
		slice.states.create("hover");

		let defaultState = slice.defaultState;
		defaultState.properties.shiftRadius = 0;

		slice.togglable = true;

		slice.events.on("toggled", (event) => {
			event.target.hideTooltip();
		});

		let activeState = slice.states.create("active");
		activeState.properties.shiftRadius = 0.10;

		let hiddenState = slice.hiddenState;
		hiddenState.properties.visible = true;
		hiddenState.properties.opacity = 1;

		// Create slices list
		this.slices = new ListTemplate<Slice>(slice);

		return slice;
	}

	/**
	 * (Re)validates the whole series, effectively causing it to redraw.
	 *
	 * @ignore Exclude from docs
	 */
	public validate(): void {

		if (Math.abs(this.startAngle - this.endAngle) < 0.01) {
			return;
		}

		// so that radius would be updated
		if (this.chart.invalid) {
			this.chart.validate();
		}

		this._leftItems = [];
		this._rightItems = [];

		this._currentStartAngle = this.startAngle;
		this._arcRect = $math.getArcRect(this.startAngle, this.endAngle);

		this._maxRadiusPercent = 0;
		for (let i = this.startIndex; i < this.endIndex; i++) {
			let dataItem = this.dataItems.getIndex(i);
			let radiusValuePercent = dataItem.values.radiusValue.percent;
			if (radiusValuePercent > this._maxRadiusPercent) {
				this._maxRadiusPercent = radiusValuePercent;
			}
		}

		super.validate();

		if (this.alignLabels) {
			if (this.startAngle > this.endAngle) {
				this._rightItems.reverse();
			}
			else {
				this._leftItems.reverse()
			}
			this.arrangeLabels(this._rightItems);
			this.arrangeLabels(this._leftItems);
		}
	}

	/**
	 * Validates data item's element, effectively redrawing it.
	 *
	 * @ignore Exclude from docs
	 * @param {PieSeriesDataItem}  dataItem  Data item
	 */
	public validateDataElement(dataItem: this["_dataItem"]): void {
		if (this.radius > 0) {

			let percent = dataItem.values.value.percent;

			if (percent > 0) {

				dataItem.__disabled = false;

				// SLICE
				let slice: Slice = dataItem.slice;
				slice.parent = this.slicesContainer;
				slice.radius = this.radius;

				if ($type.isNumber(dataItem.radiusValue)) {
					slice.radius *= dataItem.values.radiusValue.percent / this._maxRadiusPercent;
				}

				slice.innerRadius = this.innerRadius;
				slice.startAngle = this._currentStartAngle;

				if (slice.fill == undefined) {
					slice.fill = this.colors.getIndex(dataItem.index * this.colors.step);
				}
				if (slice.stroke == undefined) {
					slice.stroke = this.colors.getIndex(dataItem.index * this.colors.step);
				}

				slice.arc = dataItem.values.value.percent * (this.endAngle - this.startAngle) / 100;

				// LABEL
				let label = dataItem.label;
				label.parent = this.labelsContainer;

				let tick = dataItem.tick;
				tick.parent = this.ticksContainer;

				let normalizedMiddleAngle: number = (slice.middleAngle + 360) % 360; // force angle to be 0 - 360;

				let point: IPoint;

				if (this.alignLabels) {
					let x: number = tick.length + label.radius;

					label.verticalCenter = "middle";
					let arcRect = this._arcRect;
					// right half
					if (normalizedMiddleAngle >= 270 || normalizedMiddleAngle <= 91) { // 91 makes less chances for flickering
						x += (arcRect.width + arcRect.x) * this.radius;
						label.horizontalCenter = "left";
						this._rightItems.push(dataItem);
					}
					// left half
					else {
						x -= arcRect.x * this.radius;
						label.horizontalCenter = "right";
						this._leftItems.push(dataItem);
						x *= -1;
					}

					let distance = this.radius + tick.length + label.radius;
					point = { x: x, y: slice.iy * distance };
				}
				else {
					let x: number = slice.ix * slice.radius;
					let y: number = slice.iy * slice.radiusY;
					point = label.fixPoint({ x: x, y: y }, slice.radius);
				}

				label.moveTo(point);

				this._currentStartAngle += slice.arc;

				// Apply accessibility
				if (this.itemsFocusable()) {
					slice.role = "menuitem";
					slice.focusable = true;
				}
				else {
					slice.role = "listitem";
					slice.focusable = false;
				}

				// Apply screen reader label
				if (slice.focusable) {
					slice.events.once("focus", (ev) => {
						slice.readerTitle = this.populateString(this.itemReaderText, dataItem);
					});
					slice.events.once("blur", (ev) => {
						slice.readerTitle = "";
					});
				}
				if (slice.hoverable) {
					slice.events.once("over", (ev) => {
						slice.readerTitle = this.populateString(this.itemReaderText, dataItem);
					});
					slice.events.once("out", (ev) => {
						slice.readerTitle = "";
					});
				}

				// do this at the end, otherwise bullets won't be positioned properly
				super.validateDataElement(dataItem);
			}
			else {
				dataItem.__disabled = true;
			}
		}
	}

	/**
	 * Arranges slice labels according to position settings.
	 *
	 * @ignore Exclude from docs
	 * @param {this["_dataItem"][]}  dataItems  Data items
	 */
	protected arrangeLabels(dataItems: this["_dataItem"][]): void {
		for (let i: number = 0; i < dataItems.length; i++) {
			let dataItem: this["_dataItem"] = dataItems[i];

			let label: AxisLabelCircular = dataItem.label;

			if (label) {
				if (i < dataItems.length - 1) {
					let nextLabel: AxisLabelCircular = this.getNextLabel(i + 1, dataItems);

					if (label.invalid) {
						label.validate();
					}
					let bottom: number = label.pixelY + label.measuredHeight;

					if (nextLabel) {
						if (nextLabel.y < bottom) {
							nextLabel.y = bottom;
						}
					}
				}
			}
		}
	}

	/**
	 * Returns the next label according to `index`.
	 * 
	 * @param  {number}              index      Current index
	 * @param  {PieSerisDataItem[]}  dataItems  Data items
	 * @return {AxisLabelCircular}              Label element
	 */
	protected getNextLabel(index: number, dataItems: this["_dataItem"][]): AxisLabelCircular {
		if (dataItems.length >= index) {
			let nextDataItem: this["_dataItem"] = dataItems[index];
			if (nextDataItem) {
				if (nextDataItem.label) {
					return nextDataItem.label;
				}
				else {
					return this.getNextLabel(index + 1, dataItems);
				}
			}
		}
	}

	/**
	 * Outer radius for the series' slices in pixels.
	 *
	 * @ignore Exclude from docs
	 * @todo Redo so that users can set it
	 * @param {number}  value  Radius
	 */
	public set radius(value: number) {
		this.setPropertyValue("radius", value, true);
	}

	/**
	 * @ignore Exclude from docs
	 * @return {number} Radius
	 */
	public get radius(): number {
		return this.getPropertyValue("radius");
	}

	/**
	 * Inner radius for the series' slices in pixels.
	 *
	 * @ignore Exclude from docs
	 * @todo Redo so that users can set it
	 * @param {number}  value  Radius
	 */
	public set innerRadius(value: number) {
		this.setPropertyValue("innerRadius", value, true);
	}

	/**
	 * @ignore Exclude from docs
	 * @return {number} Radius
	 */
	public get innerRadius(): number {
		return this.getPropertyValue("innerRadius");
	}

	/**
	 * Start angle for the series' slices in degrees. (0-360)
	 *
	 * @ignore Exclude from docs
	 * @todo Redo so that users can set it
	 * @param {number}  value  Angle
	 */
	public set startAngle(value: number) {
		this.setPropertyValue("startAngle", $math.normalizeAngle(value), true);
	}

	/**
	 * @ignore Exclude from docs
	 * @return {number} Angle
	 */
	public get startAngle(): number {
		return this.getPropertyValue("startAngle");
	}

	/**
	 * End angle for the series' slices in degrees. (0-360)
	 *
	 * @ignore Exclude from docs
	 * @todo Redo so that users can set it
	 * @param {number}  value  Angle
	 */
	public set endAngle(value: number) {
		this.setPropertyValue("endAngle", value, true);
	}

	/**
	 * @ignore Exclude from docs
	 * @return {number} Angle
	 */
	public get endAngle(): number {
		return this.getPropertyValue("endAngle");
	}

	/**
	 * Align labels into nice vertical columns?
	 *
	 * This will ensure that labels never overlap with each other.
	 *
	 * Arranging labels into columns makes them more readble, and better user
	 * experience.
	 *
	 * If set to `false` labels will be positioned at `label.radius` distance,
	 * and may, in some cases, overlap.
	 *
	 * @default true
	 * @param {boolean}  value  Align labels?
	 */
	public set alignLabels(value: boolean) {
		this.setPropertyValue("alignLabels", value, true);
	}

	/**
	 * @return {boolean} Align labels?
	 */
	public get alignLabels(): boolean {
		return this.getPropertyValue("alignLabels");
	}

	/**
	 * A color set to be used for slices.
	 * 
	 * For each new subsequent slice, the chart will assign the next color in
	 * this set.
	 * 
	 * @param {ColorSet}  value  Color set
	 */
	public set colors(value: ColorSet) {
		this.setPropertyValue("colors", value, true);
	}

	/**
	 * @return {ColorSet} Color set
	 */
	public get colors(): ColorSet {
		return this.getPropertyValue("colors");
	}

	/**
	 * Binds related legend data item's visual settings to this series' visual
	 * settings.
	 *
	 * @ignore Exclude from docs
	 * @param {Container}          marker    Container
	 * @param {this["_dataItem"]}  dataItem  Data item
	 */
	public createLegendMarker(marker: Container, dataItem?: this["_dataItem"]): void {
		$iter.each(marker.children.iterator(), (child) => {
			let slice: Slice = dataItem.slice;
			// todo: make an easy possibility to bind visual properties
			child.bind("fill", slice);
			child.bind("stroke", slice);
			child.bind("fillOpacity", slice);
			child.bind("strokeOpacity", slice);
		});
	}

	/**
	 * Positions series bullet.
	 *
	 * @ignore Exclude from docs
	 * @param {Bullet}  bullet  Bullet
	 */
	public positionBullet(bullet: Bullet): void {
		super.positionBullet(bullet);

		let dataItem: this["_dataItem"] = <this["_dataItem"]>bullet.dataItem;
		let slice: Slice = dataItem.slice;

		let location = 1;
		if ($type.isNumber(bullet.locationX)) {
			location = bullet.locationX;
		}
		if ($type.isNumber(bullet.locationY)) {
			location = bullet.locationY;
		}

		bullet.moveTo({ x: slice.ix * slice.radius * slice.scale * location, y: slice.iy * slice.radius * slice.scale * location });
	}

	/**
	 * Repositions bullets when slice's size changes.
	 *
	 * @ignore Exclude from docs
	 * @param {AMEvent<Slice, ISpriteEvents>["propertychanged"]}  event  Event
	 */
	protected handleSliceScale(event: AMEvent<Slice, ISpriteEvents>["propertychanged"]): void {
		let slice: Slice = event.target;
		let dataItem: this["_dataItem"] = <this["_dataItem"]>slice.dataItem;
		$iter.each(dataItem.bullets.iterator(), (a) => {
			let value = a[1];
			this.positionBullet(value);
		});
	}

	/**
	 * Repositions bullet and labels when slice moves.
	 *
	 * @ignore Exclude from docs
	 * @param {AMEvent<Slice, ISpriteEvents>["propertychanged"]}  event  Event
	 */
	protected handleSliceMove(event: AMEvent<Slice, ISpriteEvents>["propertychanged"]): void {
		if (!this.alignLabels) {
			let slice: Slice = event.target;
			let dataItem: this["_dataItem"] = <this["_dataItem"]>slice.dataItem;
			// moving textelement, as label dx and dy are already employed for aligning
			//@labeltodo
			dataItem.label.dx = slice.dx + slice.pixelX;
			dataItem.label.dy = slice.dy + slice.pixelY;
		}
	}

	/**
	 * Copies all properties from another instance of [[PieSeries]].
	 * 
	 * @param {ColumnSeries}  source  Source series
	 */
	public copyFrom(source: this): void {
		super.copyFrom(source);
		this.slices.template.copyFrom(source.slices.template);
		this.labels.template.copyFrom(source.labels.template);
		this.ticks.template.copyFrom(source.ticks.template);
	}

}

/**
 * Register class in system, so that it can be instantiated using its name from
 * anywhere.
 * 
 * @ignore
 */
registry.registeredClasses["PieSeries"] = PieSeries;
registry.registeredClasses["PieSeriesDataItem"] = PieSeriesDataItem;


